import { createApp } from "vue";
import router from "./router";
import App from "./App.vue";
import axios from "axios";
import store from "./store";
import VueAxios from "vue-axios";
import { axiosInstance } from "./plugins/axios";
// global styles
import "./assets/scss/style.scss";

// Outside click event
const clickOutside = {
  beforeMount: (el, binding) => {
    el.clickOutsideEvent = (event) => {
      if (!(el == event.target || el.contains(event.target))) {
        binding.value();
      }
    };
    document.addEventListener("click", el.clickOutsideEvent);
  },
  unmounted: (el) => {
    document.removeEventListener("click", el.clickOutsideEvent);
  },
};

// Outside click event
const tooltip = {
  mounted: (el) => {
    el.parentElement.offsetWidth > el.previousSibling.offsetWidth + 20 &&
      el.remove();
  },
};

// check mobile
const checkMobile = (width) => {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    ) ||
    width < 992
  ) {
    store.dispatch("mobileCheck", true);
  } else {
    store.dispatch("mobileCheck", false);
  }
};

checkMobile(window.innerWidth);
// window.addEventListener("resize", () => {
//   checkMobile(window.innerWidth);
//   console.log(1);
// });

const app = createApp(App);
app
  .directive("click-outside", clickOutside)
  .directive("tooltip", tooltip)
  .use(router)
  .use(VueAxios, axios)
  .use(store);
app.config.globalProperties.$isMobile = store.state.isMobile;
app.config.globalProperties.$api = axiosInstance;
app.mount("#app");
